ding-top: 75px !important;\r\n}\n.big-padding-bottom {\r\n    padding-bottom: 85px !important;\r\n}\n/* Targets all elements */\n[class*='col-'] {\r\n    float: left;\r\n    padding: 20px;\r\n}\n#clients .col-2-3 [class*='col-'] {\r\n    padding: 0;\r\n}\n/* Clearfix */\n.clearfix:after {\r\n    content: \"\";\r\n    display: table;\r\n    clear: both;\r\n}\n/* Main Widths */\n.col-1 {\r\n    width: 100%;\r\n}\n.col-2 {\r\n    width: 50%;\r\n}\n.col-3 {\r\n    width: 33.33%;\r\n}\n.col-4 {\r\n    width: 25%;\r\n}\n.col-5 {\r\n    width: 20%;\r\n}\n.col-6 {\r\n    width: 16.6666666667%;\r\n}\n.col-7 {\r\n    width: 14.2857142857%;\r\n}\n.col-8 {\r\n    width: 12.5%;\r\n}\n.col-9 {\r\n    width: 11.1111111111%;\r\n}\n.col-10 {\r\n    width: 10%;\r\n}\n.col-11 {\r\n    width: 9.09090909091%;\r\n}\n.col-12 {\r\n    width: 8.33%;\r\n}\n.col-2-3 {\r\n    width: 66.66%;\r\n}\n.col-3-4 {\r\n    width: 75%;\r\n}\n.col-9-10 {\r\n    width: 90%;\r\n}\n/* Golden Ratio */\n.col-61 {\r\n    width: 61.8%;\r\n}\n.col-38 {\r\n    width: 38.2%;\r\n}\n/* --------- 2.1 Header --------- */\n#header {\r\n    height: 71px !important;\r\n    overflow: visible;\r\n    z-index: 9999;\r\n    width: 100%;\r\n    position: absolute !important;\r\n}\n#header .row {\r\n    padding: 0;\r\n}\n#header aside {\r\n    text-align: right;\r\n}\n#header ul {\r\n    text-align: center;\r\n}\n#header li {\r\n    display: inline-block;\r\n    list-style: none;\r\n    margin: 0;\r\n}\n/* --------- 2.2 Logo ---------- */\n#logo {\r\n    float: left;\r\n    height: 71px;\r\n    line-height: 66px;\r\n    margin-right: 15px;\r\n}\n#logo h1, #logo h2 {\r\n    display: inline-block;\r\n}\n#banner #logo h1 {\r\n    font-size: 28px;\r\n    margin-right: 10px;\r\n    font-weight: 900;\r\n    padding: 0;\r\n}\n#logo h2 {\r\n    font-size: 18px;\r\n    padding: 0;\r\n}\n#logo img {\r\n    max-height: 22px;\r\n    vertical-align: middle;\r\n    margin-right: 15px;\r\n}\n#navigation-logo {\r\n    display: none;\r\n}\n.na