ative;\r\n}\n.icon-block h4 {\r\n    font-weight: bold;\r\n    padding-top: 0;\r\n}\n.icon-block .icon {\r\n    position: absolute;\r\n}\n.icon-block p {\r\n    margin-top: 0;\r\n}\n/* Icon Left*/\n.icon-left .icon {\r\n    left: 15%;\r\n}\n.icon-left .icon-block-description {\r\n    padding-left: 53px;\r\n}\n/* Icon Right */\n.icon-right .icon {\r\n    right: 15%;\r\n}\n.icon-right .icon-block-description {\r\n    padding-right: 53px;\r\n}\n/* Icon Above */\n.icon-top {\r\n    display: block;\r\n}\n.icon-top .icon {\r\n    position: relative;\r\n    display: block;\r\n}\n.icon-top .icon-block-description {\r\n    padding-top: 25px;\r\n}\n/* --------- 5.2 Parallax Elements ---------- */\n.banner-parallax-1, .banner-parallax-2, .banner-parallax-3 {\r\n    min-height: 350px;\r\n}\n/* --------- 5.3 Divider ---------- */\n.divider {\r\n    position: relative;\r\n    width: 40%;\r\n    height: 20px;\r\n    line-height: 20px;\r\n    margin: 10px auto 5px auto;\r\n    clear: both;\r\n    text-align: center;\r\n}\n.divider-inner {\r\n    position: absolute;\r\n    height: 1px;\r\n    width: 100%;\r\n    top: 50%;\r\n    margin-top: -1px;\r\n    border-top: 1px solid;\r\n    border-color: #e1e1e1;\r\n}\n.divider i {\r\n    background: #fff;\r\n    position: relative;\r\n    top: -11px;\r\n    padding: 0 5px 0 5px;\r\n    color: #e1e1e1;\r\n}\n/*------------------------------------------------------------------------------------------*/\n/* 6. Landing Page Sections */\n/*------------------------------------------------------------------------------------------*/\n/* --------- 6.1 Introduction ---------- */\n.introduction img.featured {\r\n    padding: 55px 0 0 0;\r\n}\n/* --------- 6.2 Features ---------- */\n.features.row {\r\n    padding-left: 5%;\r\n    padding-right: 5%;\r\n}\n.features div:nth-child(4) {\r\n    clear: both;\r\n}\n/* --------- 6.3 Testimonials ---------- */\n.testimonial {\r\n    padding: 15px;\r\n}\nblockquote {\r\n    position: relative;\r\n}\n.testimon