r\n        padding-right: 20px;\r\n    }\r\n\r\n    /*-----------------Sections-----------------*/\r\n\r\n    /* Video */\r\n    .slvj-lightbox iframe, .slvj-lightbox object, .slvj-lightbox embed {\r\n        height: 370px !important;\r\n    }\r\n\r\n    /* Footer */\r\n    #landing-footer #copyright {\r\n        float: left;\r\n        width: 50%;\r\n    }\r\n\r\n    #landing-footer .social-icons {\r\n        float: right;\r\n        width: 50%;\r\n    }\r\n\r\n}\n/* Landscape Tablet to Desktop\r\n===============================*/\n@media (min-width: 768px) and (max-width: 1024px) {\r\n\r\n    #wrapper {\r\n        margin:0;\r\n        padding:0;\r\n    }\r\n\r\n    .page-border {\r\n        display:none;\r\n    }\r\n\r\n    .big-padding-top {\r\n        padding-top: 45px !important;\r\n    }\r\n\r\n    /*-----------------Header Elements-----------------*/\r\n    #header aside {\r\n        display: inline-block;\r\n        position: absolute;\r\n        top: 40px;\r\n        right: 60px;\r\n        padding-top:3px;\r\n        padding-right: 5px;\r\n    }\r\n\r\n    #header.nav-solid aside {\r\n        top: 20px;\r\n    }\r\n\r\n    #header aside ul {\r\n        margin-top: 0 !important;\r\n        padding-top: 6px;\r\n    }\r\n\r\n    /*Navigation*/\r\n    nav#nav-mobile ul {\r\n        margin-left: -40px;\r\n        margin-right: -40px;\r\n        padding-left: 20px;\r\n        padding-right: 20px;\r\n    }\r\n\r\n    /*-----------------Sections-----------------*/\r\n\r\n    /* Video */\r\n    .slvj-lightbox iframe, .slvj-lightbox object, .slvj-lightbox embed {\r\n        height: 432px !important;\r\n    }\r\n}\n/*Namari Color CSS*/\n/* Namari Landing Page Dynamic Style Index\r\n\r\n1. Website Default Styling\r\n2. Navigation\r\n3. Primary and Secondary Colors\r\n4. Banner\r\n5. Typography\r\n6. Buttons\r\n7. Footer\r\n\r\n\r\n/*------------------------------------------------------------------------------------------*/\n/* 1. Website Default Styling */\n/*------------------------------------------------------------------------------------------*/\nbody {\r\n    background:#fff;\r\n}\n/* Default Link Color */\na, .la-ball-triangle-path {\r\n    color:#d2b356;\r\n}\na:hover, #header.nav-solid nav a:hover {\r\n    color:#d2b356;\r\n}\n/* Default Icon Color */\n.icon i {\r\n    color:#d2b356;\r\n}\n/* Border Color */\n#banner .section-heading:before, .testimonial.classic footer:before {\r\n    background: #d2b356;\r\n}\n.pricing-block-content:hover {\r\n    border-color:#d2b356;\r\n}\n/*------------------------------------------------------------------------------------------*/\n/* 2. Navigation */\n/*------------------------------------------------------------------------------------------*/\n/* Transparent Navigation Color on a Banner */\n#header nav a, #header i {\r\n    color:#111;\r\n}\n/* Navigation Colors when the Navigation is sticky and solid */\n#header.nav-solid, #header.nav-solid a, #header.nav-solid i, #nav-mobile ul li a {\r\n    color:#333;\r\n}\n/* Navigation Active State */\n#header.nav-solid .active {\r\n    color: #d2b356;\r\n    border-color: #d2b356;\r\n}\n/*------------------------------------------------------------------------------------------*/\n/* 3. Primary and Secondary Colors */\n/*------------------------------------------------------------------------------------------*/\n/* Primary Background and Text Colors */\n.primary-color, .featured .pricing {\r\n    background-color:#d2b356;\r\n}\n.primary-color, .primary-color .section-title, .primary-color .section-subtitle, .featured .pricing, .featured .pricing p {\r\n    color:#fff;    \r\n}\n.section-heading h2:after {\r\n    background:#d2b356;\r\n    content:\"\";\r\n    display:block;\r\n    width:30px;\r\n    height:5px;\r\n    margin-top:30px;\r\n}\n.text-center .section-heading h2:after {\r\n    margin:30px auto 25px auto;\r\n}\n/* Primary Icon Colors */\n.primary-color .icon i, .primary-color i {\r\n    color:#fff;\r\n}\n/* Secondary Background and Text Colors */\n.secondary-color {\r\n    background-color:#f5f5f5;\r\n}\n/*------------------------------------------------------------------------------------------*/\n/* 4. Banner */\n/*------------------------------------------------------------------------------------------*/\n/* Banner Background and Text Colors */\n#banner {\r\n    background: url('banner-image-1.jpg') no-repeat center top;\r\n    background-size:cover;\r\n}\n/*------------------------------------------------------------------------------------------*/\n/* 5. Typography */\n/*------------------------------------------------------------------------------------------*/\nbody {\r\n    font-family: 'Open Sans', sans-serif, Arial, Helvetica;\r\n    font-size:15px;\r\n    font-weight:normal;\r\n    color:#111;\r\n}\n/* Logo, if you are using Fonts as Logo and not image\r\n\r\n#logo h1 {\r\n    font-family:;\r\n    font-size:; \r\n    font-weight:;\r\n    color:;\r\n}\r\n\r\n#logo h2 {\r\n    font-family:;\r\n    font-size:; \r\n    font-weight:;\r\n    color:;\r\n}\r\n\r\n*/\n/* Banner Typography */\n#banner h1 {\r\n    font-family: 'Open Sans', sans-serif, Arial, Helvetica;\r\n    font-size:62px;\r\n    line-height:60px;\r\n    font-weight:800;\r\n    color:#111;\r\n}\n#banner h2 {\r\n    font-family: 'Open Sans', sans-serif, Arial, Helvetica;\r\n    font-size:18px;\r\n    font-weight:300;\r\n    color:#111;\r\n}\n/* Section Title and Subtitle */\n.section-title {\r\n    font-family: 'Open Sans', sans-serif, Arial, Helvetica;\r\n    font-size: 34px; \r\n    font-weight:700;\r\n    color:#111;\r\n}\n.section-subtitle {\r\n    font-family: 'Open Sans', sans-serif, Arial, Helvetica;\r\n    font-size: 16px;\r\n    font-weight:300;\r\n    color:#9c9c9c;\r\n}\n/* Testimonial */\n.testimonial q {\r\n    font-family: 'Open Sans', sans-serif, Arial, Helvetica;\r\n    font-size: 17px; \r\n    font-weight:300;\r\n}\n.testimonial.classic q, .testimonial.classic footer {\r\n    color:#111;\r\n}\n/* Standard Headings h1-h6 */\nh1 {\r\n    font-family: 'Open Sans', sans-serif, Arial, Helvetica;\r\n    font-size: 40px; \r\n    font-weight:300;\r\n    color:#111;\r\n}\nh2 {\r\n    font-family: 'Open Sans', sans-serif, Arial, Helvetica;\r\n    font-size: 34px; \r\n    font-weight:300;\r\n    color:#111;\r\n}\nh3 {\r\n    font-family: 'Open Sans', sans-serif, Arial, Helvetica;\r\n    font-size: 30px; \r\n    font-weight:700;\r\n    color:#111;\r\n}\nh4 {\r\n    font-family: 'Open Sans', sans-serif, Arial, Helvetica;\r\n    font-size: 18px; \r\n    font-weight:400;\r\n    color:#111;\r\n}\nh5 {\r\n    font-family: 'Open Sans', sans-serif, Arial, Helvetica;\r\n    font-size: 16px; \r\n    font-weight:400;\r\n    color:#111;\r\n}\nh6 {\r\n    font-family: 'Open Sans', sans-serif, Arial, Helvetica;\r\n    font-size: 14px; \r\n    font-weight:400;\r\n    color:#111;\r\n}\n/*------------------------------------------------------------------------------------------*/\n/* 6. Buttons */\n/*------------------------------------------------------------------------------------------*/\n/* ----------Default Buttons---------- */\n/* Button Text */\n.button, input[type=\"submit\"]  {\r\n    font-family: 'Open Sans', sans-serif, Arial, Helvetica;\r\n    font-size:14px;\r\n    font-weight:bold;\r\n    color:#111;\r\n}\n/* Button Color */\n.button, input[type=\"submit\"] {\r\n    border-color:#111;\r\n}\n/* Button Hover Color */\n.button:hover,  input[type=\"submit\"]:hover {\r\n    border-color:#d2b356;\r\n    color:#d2b356;\r\n}\n/* ----------Banner Buttons---------- */\n/* Button Text */\n#banner .button {\r\n    font-family: 'Open Sans', sans-serif, Arial, Helvetica;\r\n    font-size:16px;\r\n    color:#111;\r\n}\n/* Button Color */\n#banner .button {\r\n    border-color:#111;\r\n}\n/* Button Hover Color */\n#banner .button:hover {\r\n    color:#d2b356;\r\n    border-color:#d2b356;\r\n}\n/*------------------------------------------------------------------------------------------*/\n/* 